import React, { useState } from 'react';
import './about.css';
import Modal from "./modal";
import Video from "../assets/3d_video.mp4"
import { Link } from "gatsby";


const data_sources = [
  { image: "img/project1.jpg", category: "civil"},
  { image: "img/project2.jpg", category: "mining"},
  { image: "img/project3.jpg", category: "civil"},
  { image: "img/project1.jpg", category: "minig"},
  { image: "img/project2.jpg", category: "civil"},
  { image: "img/project3.jpg", category: "minig"},
  { image: "img/project1.jpg", category: "7"},
  { image: "img/project2.jpg", category: "8"},
  { image: "img/project3.jpg", category: "9"},
  { image: "img/project1.jpg", category: "10"},
  { image: "img/project2.jpg", category: "11"},
  { image: "img/project3.jpg", category: "12"},
  { image: "img/project3.jpg", category: "13"},
  { image: "img/project3.jpg", category: "14"},
  { image: "img/project3.jpg", category: "15"},
  { image: "img/project3.jpg", category: "16"},
  { image: "img/project3.jpg", category: "17"},
  { image: "img/project3.jpg", category: "18"},
  { image: "img/project3.jpg", category: "19"},
  { image: "img/project3.jpg", category: "20"},
  { image: "img/project3.jpg", category: "21"},
  { image: "img/project3.jpg", category: "22"},
  { image: "img/project3.jpg", category: "23"},
  { image: "img/project3.jpg", category: "24"},
];

const Project_image_garelly = () => {
  
  const [clicked_index, setClicked_index] = useState();
  const [array_wrap, setArray_wrap] = useState(1);

  const sendIndex = (index, groupNumber) => {
    let max_number = (groupNumber*12 + 12) - 1;
    let pressedIndex ;
    if(groupNumber === 0) {
      pressedIndex = index;
    }
    else {
      pressedIndex = (groupNumber*12) + index;
    }
    // console.log("max " + max_number);
    // console.log(pressedIndex);
    let modal = <Modal 
    clicked_index={pressedIndex}
    data_sources={data_sources}
    close_modal={() => setClicked_index(null)}
    max_number = {max_number}
    />;
    setClicked_index(modal);
  }



  return(
    <>
      {clicked_index}

      <div className="garelly_container">
        <div className="inner_container">
          <h1>Our Projects</h1>
            <div>
              {Array.from({length: array_wrap}, (_, groupNumber) => <div className="garelly" key={groupNumber}>
                {data_sources.slice(groupNumber * 12, groupNumber * 12 + 12).map((e, i) => 
                  <div 
                    className="img_box"
                    key={i}
                    onClick={() => sendIndex(i, groupNumber)}
                  >
                    <img 
                    src={e.image} 
                    alt="Project images"
                    />
                  </div>
                )}
              </div>)}
            </div>
            <button
              className="show_more fontTwo"
              onClick={() => setArray_wrap(array_wrap + 1)}>
                Show More  <i className="fas fa-caret-down fontTwo"></i>
            </button>
        </div>
      </div>
    </>
  );
};


const About = () => {
  
  return (
    <div id="about">
      <Project_image_garelly />
      
      <div className="aboutContainer">
        <div className="flex_right flexHeight">
            <video autoPlay loop preload="auto">
              <source src={Video} type="video/mp4"></source>
              <track default/>
            </video>
        </div>
        <div className="flex_left flexHeight">
          <div className="fontFour textContainer">
            <h2>Our Services</h2>
            <p>Our mission is your wish</p>
            <Link to="/service">
              <i className="fas fa-angle-double-left">
              </i>  Learn more 
            </Link>
          </div>
        </div>
      </div>
    </div>
  )
}

export default About;